// ============================
// –õ—ñ–Ω–∏–≤–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –∑–æ–±—Ä–∞–∂–µ–Ω—å <img>
// ============================

// 1Ô∏è‚É£ –ó–Ω–∞—Ö–æ–¥–∏–º–æ –≤—Å—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è, —è–∫—ñ –º–∞—é—Ç—å –∞—Ç—Ä–∏–±—É—Ç `data-src` (–∞ –Ω–µ `src`).
const lazyImages = document.querySelectorAll('img[data-src]');

// 2Ô∏è‚É£ –§—É–Ω–∫—Ü—ñ—è, —è–∫–∞ –±—É–¥–µ –ø—ñ–¥–≤–∞–Ω—Ç–∞–∂—É–≤–∞—Ç–∏ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
const lazyLoadImages = (entries, observer) => {
  entries.forEach(entry => {
    // –Ø–∫—â–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –∑–Ω–∞—Ö–æ–¥–∏—Ç—å—Å—è —É –≤–∏–¥–∏–º—ñ–π —á–∞—Å—Ç–∏–Ω—ñ –µ–∫—Ä–∞–Ω—É (intersecting)
    if (entry.isIntersecting) {
      const img = entry.target; // –û—Ç—Ä–∏–º—É—î–º–æ <img>, —è–∫–µ —Ç—Ä–µ–±–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏
      img.src = img.dataset.src; // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ —à–ª—è—Ö –∑ `data-src` —É `src`
      img.removeAttribute('data-src'); // –í–∏–¥–∞–ª—è—î–º–æ `data-src`, —â–æ–± –Ω–µ –∑–∞–≤–∞–Ω—Ç–∞–∂—É–≤–∞—Ç–∏ –∑–Ω–æ–≤—É
      observer.unobserve(img); // –ü—Ä–∏–±–∏—Ä–∞—î–º–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –∑—ñ —Å–ø–∏—Å–∫—É —Å–ø–æ—Å—Ç–µ—Ä–µ–∂–µ–Ω–Ω—è
    }
  });
};

// 3Ô∏è‚É£ –ù–∞–ª–∞—à—Ç—É–≤–∞–Ω–Ω—è Intersection Observer –¥–ª—è –∑–æ–±—Ä–∞–∂–µ–Ω—å
const imgObserver = new IntersectionObserver(lazyLoadImages, {
  root: null, // `null` –æ–∑–Ω–∞—á–∞—î, —â–æ –≤—ñ–¥—Å—Ç–µ–∂—É—î–º–æ –≤–µ—Å—å viewport (–µ–∫—Ä–∞–Ω)
  threshold: 0.1, // –ó–∞–ø—É—Å–∫–∞—î–º–æ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è, –∫–æ–ª–∏ 10% –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –∑'—è–≤–∏—Ç—å—Å—è —É –ø–æ–ª—ñ –∑–æ—Ä—É
});

// 4Ô∏è‚É£ –î–æ–¥–∞—î–º–æ –≤—Å—ñ –∑–Ω–∞–π–¥–µ–Ω—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –¥–æ —Å–ø–æ—Å—Ç–µ—Ä–µ–∂–µ–Ω–Ω—è
lazyImages.forEach(img => imgObserver.observe(img));

// ============================
// –õ—ñ–Ω–∏–≤–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Ñ–æ–Ω–æ–≤–æ–≥–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è (background-image)
// ============================

// 1Ô∏è‚É£ –ó–Ω–∞—Ö–æ–¥–∏–º–æ –≤—Å—ñ –µ–ª–µ–º–µ–Ω—Ç–∏ –∑ `data-bg`
const lazyBackgrounds = document.querySelectorAll('.lazy-bg');

// 2Ô∏è‚É£ –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—ñ–¥–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Ñ–æ–Ω—ñ–≤
const lazyLoadBackgrounds = (entries, observer) => {
  entries.forEach(entry => {
    if (entry.isIntersecting) {
      const bg = entry.target; // –û—Ç—Ä–∏–º—É—î–º–æ –µ–ª–µ–º–µ–Ω—Ç, —è–∫–∏–π –º–∞—î `data-bg`
      bg.style.backgroundImage = `url(${bg.dataset.bg})`; // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ —à–ª—è—Ö —É background-image
      bg.removeAttribute('data-bg'); // –í–∏–¥–∞–ª—è—î–º–æ `data-bg`, —â–æ–± –Ω–µ –ø—ñ–¥–≤–∞–Ω—Ç–∞–∂—É–≤–∞—Ç–∏ –∑–Ω–æ–≤—É
      observer.unobserve(bg); // –ü—Ä–∏–±–∏—Ä–∞—î–º–æ –∑—ñ —Å–ø–∏—Å–∫—É —Å–ø–æ—Å—Ç–µ—Ä–µ–∂–µ–Ω–Ω—è
    }
  });
};

// 3Ô∏è‚É£ –ù–∞–ª–∞—à—Ç—É–≤–∞–Ω–Ω—è Intersection Observer –¥–ª—è —Ñ–æ–Ω—ñ–≤
const bgObserver = new IntersectionObserver(lazyLoadBackgrounds, {
  root: null, // –í—ñ–¥—Å—Ç–µ–∂—É—î–º–æ –≤–µ—Å—å viewport
  threshold: 0.1, // –ó–∞–ø—É—Å–∫–∞—î–º–æ, –∫–æ–ª–∏ 10% –±–ª–æ–∫—É —É –ø–æ–ª—ñ –∑–æ—Ä—É
});

// 4Ô∏è‚É£ –î–æ–¥–∞—î–º–æ –≤—Å—ñ –∑–Ω–∞–π–¥–µ–Ω—ñ –µ–ª–µ–º–µ–Ω—Ç–∏ –¥–æ —Å–ø–æ—Å—Ç–µ—Ä–µ–∂–µ–Ω–Ω—è
lazyBackgrounds.forEach(bg => bgObserver.observe(bg));
